import React, { useState } from "react";
import ReactDOM from "react-dom";
import styled, {
  css,
  ThemeProvider,
  createGlobalStyle
} from "styled-components";

import App from "./App";
import Header from "./components/Header";
import { $nav_transition } from "./mixins";

const GlobalStyle = createGlobalStyle`
  body, html {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
  }
`;

export const Theme = {
  layout: {
    padding: "15px"
  },
  colors: {
    lightestGrey: "rgba(0,0,0, 0.14)",
    lighterGrey: "rgba(0,0,0, 0.3)",
    lightGrey: "rgba(0,0,0, 0.6)",
    medGrey: "#3c4043",
    darkGrey: "rgba(0,0,0, 0.87)",
    lightRed: "rgba(255, 82, 82, 0.8)",
    red: "rgba(255, 82, 82, 1)"
  }
};

const Overlay = styled.div`
  ${({ navIsHidden }) => css`
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgb(33, 33, 33);
    opacity: 0.48;
    transition: opacity ${$nav_transition}, visibility ${$nav_transition};
    ${navIsHidden
      ? css`
          opacity: 0;
          visibility: hidden;
        `
      : ``}
    @media (min-width: 730px) {
      display: none;
    }
  `}
`;

const Index = () => {
  const [state, setState] = useState({ navIsHidden: true });

  const handleNavToggle = () => {
    setState(state => {
      const { navIsHidden } = state;
      return {
        navIsHidden: !navIsHidden
      };
    });
  };

  const { navIsHidden } = state;

  return (
    <ThemeProvider theme={Theme}>
      <GlobalStyle />
      <Overlay onClick={handleNavToggle} navIsHidden={navIsHidden} />
      <Header navIsHidden={navIsHidden} handleNavToggle={handleNavToggle} />
      <App />
    </ThemeProvider>
  );
};

ReactDOM.render(<Index />, document.getElementById("root"));
